.container {
    /* border: 1px solid rgb(11, 128, 128); */
    /* #0b8080 */
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    margin: 0 auto;
}
.alltext {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
    font-family: Roboto-light;
}
.titles {
    text-align: center;
}
.subTitles {
    font-size: 1.5em;
    text-shadow: 1px 1px rgb(12, 54, 54);
    color: whitesmoke;
}
.paddingTop{
    padding-top: 20px;
}
.colorTitles {
    text-shadow: 1px 2px rgb(12, 54, 54);
    color: rgb(250, 246, 246);
}
.textItalic {
    font-style: italic;
}
.readingText {
    padding: 30px;
    font-size: 1.2em;
    text-align: justify;
    line-height: 1.2em;
    color: rgb(255, 255, 255);
    text-shadow: 1px 1px rgb(5, 50, 53);
}
.containerImage {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 512px;
    margin: 0 auto;
}
.textContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 80%;
    margin: 0 auto;
}
.imgBig {
    box-shadow: 2px 3px 8px 1px rgb(49, 49, 49);
    border-radius: 5px;
    display: block;
    /* width: 100%; */
    height: 512px;
    overflow: hidden;
}
.miniText {
    font-size: 0.7em;
    padding-bottom: 20px;
    color: whitesmoke;
}
.iframeContainer {
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 85%;
    margin: 0 auto;
}
@media (max-width:720px){
    .container {
      width: 100%;
    }
    .containerImage {
        height: 200px;
    }
    .imgBig {
        height: 128px;
    }
    .titles {
        font-size: 1em;
    }
    .subTitles {
        font-size: 1.2em;
    }
    .textContainer {
        width: 100%;
    }
}