.container {
    display: flex;
    flex-direction: column;
    justify-items: center;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 450px;
    margin: 0 auto;
}
.formContainer {
    border: 1px solid black;;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 40%;
    height: 300px;
    border-radius: 5px;
    background-color: teal;
}
.form {
    display: flex;
    flex-direction: column;
    width: 60%;
    margin: 0 auto;
}
.colorText {
    color: whitesmoke;
    font-family: Roboto-light;
}
.textTitle {
    text-shadow: 1px 1px black;
    font-family: Roboto-light;
}


@media (max-width:720px){
    .container {
      width: 100%;
    }
    .formContainer {
        width: 90%;
    }
    .form {
        width: 90%;
    }
}