.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 280px;
    margin: 0 auto;
}
/* .bgimage {
    display: block;
    border-radius: 5px;
} */
.itemsContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.imgRounded {
    border-radius: 50%;
    max-width: 180px;
    max-height: 180px;
    box-shadow: 2px 2px 10px 5px rgb(8, 92, 92);
}
.ulHorizontal {
    padding-top: 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    list-style: none;
    margin: 0 auto;
}
.ulHorizontal li {
    float: inherit;
    padding-right: 18px;
}
.icon {
    max-height: 25px;
    box-shadow: 1px 1px 5px 2px rgb(8, 92, 92);
}
.textMenu {
    color: whitesmoke;
    padding-left: 5px;
    font-size: 1.2em;
    text-shadow: 1px 1px black;
}
.divRow {
    display: flex;
    flex-direction: row;
    justify-items: center;
    align-items: center;
}
.divRow:hover {
    transform: scale(1.1);
    cursor: pointer;
}
.alltext {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
    font-family: Roboto-light;
}

@media (max-width:720px){
    .container {
      width: 100%;
    }
    .ulHorizontal {
        display: none;
    }
}